@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);

.landing {
    min-height: 100vh;
    height: 100%;
    overflow: auto;

    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: row;

    background-color: #f2f2f3;
    background-size: cover;
}

.landing hr.solid {
    border-top: 100px solid black;
}

.landing .photo {
    position: absolute;
    margin-top: 7.5vh;
    margin-left: 50vh;
    width: 450px;
}

.landing .text-container {
    width: 80vw;
    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;

    margin-top: 15vh;
    margin-left: 10vw;
}


.landing .text-container h1 {
    font-family: "Roboto";
    font-style: bold;
    font-size: 2em;
    margin-top: 0px;
    margin-bottom: 10px;
}

.landing .text-container h2 {
    font-family: "Roboto";
    font-style: bold;
    font-size: 1.6em;
    margin-top: 0px;
    margin-bottom: 45px;
}

.landing .text-container h3 {
    font-family: "Roboto";
    font-style: bold;
    font-size: 1em;
    margin-bottom: 0px;
}

.landing .text-container p {
    font-family: "Roboto";
    font-style: normal;
    font-size: 1em;
    line-height: 1.35em;
    margin-top: 0px;
    margin-bottom: 0px;
}

.landing .text-container li {
    font-family: "Roboto";
    font-style: normal;
    font-size: 1em;
    margin-top: 0px;
    margin-bottom: 5px;
}

.landing .text-container .small-text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 800;
    font-size: 0.875em;
    display: flex;

    color: #000000;
}

.landing .text-container .big-text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 800;
    font-size: 2.25em;
    
    color: #000000;

    margin-top: 0.1em;
    margin-bottom: 1em;
}


.landing .button-container {
    width: 30vw; 
    
    display: flex;

    align-items: flex-end;
    justify-content: flex-end;

    flex-direction: column;
}

.landing .text-container table {
    width: 100%;
}

.landing .text-container th {
    height: 25px;
}

.landing .text-container td {
    min-width: 50px;
}

.landing .text-container tr:hover {
    min-width: 50px;
    background-color: #dfdfdf;
}

.landing .text-container tr {
    height: 50px;
}

.landing .text-container tbody {
    font-family: "Roboto";
    font-style: normal;
    font-size: 1em;
    justify-content: start;
    text-align: left;
    line-height: 1.35em;
}