.ex-button {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    height: 3.125em;
    width: 12em;

    font-family: "lato";
    font-style: normal;
    font-weight: bold;
    font-size: 0.875em;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;

    color: #000000;

    background: #3ea86c;
    border-radius: 15px;

    margin-right: 25px;
}

.ex-button:hover {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    font-family: "lato";
    font-style: normal;
    font-weight: bold;
    font-size: .875em;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;
    
    height: 3.125em;
    width: 12em;

    color: #000000;

    background: #338959;
    border-radius: 15px;

    margin-right: 25px;
}
